h1, p {
    font-family: Lato;
  }
  
  *{
    box-sizing: border-box;
  }
  
  *,
  body{
    margin: 0;
    padding: 0;
    background-color:#010059;
  }
  
  .wrapper{
    width: 600px;
    /* display: flex; */
    margin: 10% auto;
  }
  
  .alphanumeric{
    display: inline-block;
    width: 75%;
    text-align: center;
  }
  
  .specialchar{
    display: inline-block;
    width: 25%;
  }
  
  .specialchar input{
    margin-bottom: 5px;
  }
  
  input{
    width: 35px;
    height: 40px;
    color: #222831;
    margin: 0 1px 0 1px;
    padding-bottom: 5px;
    background-color: white;
    border: 2px solid black;
    border-radius: 10px;
    font-size: 15px;
  }
  
  .top_row{
    margin-bottom: 5px;
    margin-left: 30px;
  }
  
  .mid_row{
    margin-left: 25px;
    margin-bottom: 5px;
  }
  
  .last_row{
    margin-left: 25px;
  }
  
  input:focus,
  button:focus{
    outline: none;
  }
  
  input:active{
    outline: none;
    border: 2px solid black;
    background-color: black;
    color: white;
  }
  
  .backspace{
    width: 60px;
  }
  
  .caps{
    width: 60px;
  }
  
  .display{
    margin: 0px 0px 10px 0px;
    padding: 10px;
    font-size: 20px;
    width: 100%;
    height: 200px;
    font-family: lato;
    word-wrap: break-word;
    overflow: auto;
    border: 2px solid gray;
    border-radius: 10px;
    color: black;
    background-color: #eaeaea;
  }
  
  @media screen and (max-width: 500px) {
    .wrapper{
      width: 100%;
      margin: 0;
      height: 80vh;
      padding-top: 20px;
    }
  
    .display{
      width: 98%;
      margin: 10px auto;
    }
    
    .top_row{
      width: 100%;
      margin: 5px 0;
    }
  
    .mid_row{
      width: 100%;
      margin: 5px;
    }
  
    .last_row{
      width: 100%;
      margin: 0 0px;
    }
  
    input{
      width: 10%;
      font-size: 12px;
      margin: 0;
    }
  
    .caps,
    .backspace{
      width: 14.5%;
      /* margin-left: 2px; */
    }
  
    .alphanumeric{
      display: inline-block;
      width: 100%;
    }
  
    .specialchar{
      display: flex;
      width: 100%;
      flex-wrap: wrap;
      justify-content: center;
    }
  
    /* .specialchar input{
      margin-bottom: 5px;
      width: 10%;
      font-size: 12px;
    } */
  }